.barraAzul {
    margin: 6rem 0;
    background-color: #0B2737;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 3rem 0;
}

.textoBarraAzul {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.barraAzul h3 {
    color: #fff;
    font-weight: 500;
    font-size: 2.25rem;
    text-align: center;
}

.barraAzul p {
    margin-top: 1.2rem;
    color: #DBDBDB;
    text-align: center;
    width: 60%;
    line-height: 150%;
}

.divFontAwesome {
    margin-top: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 10rem;
}

.barraAzul svg {
    transition: 0.2s;
}

.barraAzul svg:hover {
    color: #add5e9;
}