.mainView{
    padding-top: 5rem;
    height: 100vh;
    background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%), #F6F6F6;
    display: flex;
    flex-direction: column;
}
.dividirMain{
    display: flex;
    height: calc(100vh - 5rem);
    justify-content: space-between;
    align-items: center;
    width: 85%;
    max-width: 81rem;
    margin-left: auto;
    margin-right: auto;
}