.escolhaDispositivo {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1rem 0.5rem;
    margin: 0 0.4rem;
    border-radius: 3px;
    border: 2px rgba(0, 0, 0, 0) solid;
    transition: 0.3s ease-in;
    cursor: pointer;
}

.escolhaDispositivo:hover {
    border: 2px black solid;
}

.escolhaDispositivo span {
    margin-top: 2rem;
    font-size: 1.1rem;
    font-weight: 500;
    transition: 0.2s ease;
    color: #333;
}

.escolhaDispositivo:hover span {
    color: black;
}