.footer {
    width: 85%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    margin: 0 auto;
}

.footerCima {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    padding-right: 2rem;
}

.footerBaixo {
    margin-top: 1rem;
    border-top: 1px black solid;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 1.5rem 0 2rem;
}