
@media(min-height: 750px) and (min-width: 1500px){
    .mockup img {
        height: 85%;
        width: unset;
    }
}

@media(max-width: 1100px){
    .mainContent h1{
        margin-top: 2rem;
        font-size: 1.7rem;
    }

    .mainContent p{
        font-size: 1.2rem;
    }
    
    .tituloFeature {
        font-size: 1.7rem;
    }
    
    .conteudoFeature {
        margin-top: 2rem;
    }

    .featureReversaIMG {
        width: 25rem;
    }

    .pedirOrcamentoOverlay p {
        font-size: 1.2rem;
    }

    .pedirOrcamentoOverlay {
        width: 100%;
    }

    .variasEscolhas {
        width: 60%;
    }
}


@media(max-width: 950px){
    .mainContent h1{
        margin-top: 2rem;
        font-size: 1.5rem;
    }

    .feature{
        flex-direction: column-reverse;
    }

    .textoFeature{
        width: 100%;
        text-align: left;
    }

    .featureIMG{
        margin: 2rem 0;
        align-self: center;
    }

}

@media(max-width: 900px){
    .mainView{
        padding-top: 1rem;
    }

    .dividirMain{
        flex-direction: column;
        align-items: flex-start;
    }

    .mainContent{
        width: 100%;
        height: unset;
    }

    .mainContent h1{
        margin-top: unset;
        font-size: 1.2rem;
    }

    .mainContentPs {
        margin-top: 1rem;
        padding-bottom: unset;
    }

    .mainContent p{
        margin-top: 0.5rem;
        font-size: 1rem;
    }

    .extensaoBotao{
        position: absolute;
        width: 85%;
        bottom: 1rem;
        z-index: 2;
    }

    .mockup{
        height: 50%;
    }

    .barraAzul h3 {
        font-size: 1.5rem;
    }
    
    .barraAzul p {
        margin-top: 2rem;
        font-size: 1rem;
        width: 85%;
    }

    .FAQScontainer {
        width: 85%;
    }

    .accordionItem h4{
        font-size: 1.2rem;
    }

    .accordionItem h4::after{
        width: 1rem;
        height: 1rem;
        font-size: 1.2rem;
    }

    .footerCima {
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding-right: 0;
    }

    .componenteFooter {
        margin-top: 1.5rem;
    }

    .redesSociaisFooter {
        margin-top: 3rem;
    }

    .footerBaixo {
        flex-direction: column;
        text-align: center;
        line-height: 150%;
    }

    .footerBaixo img{
        margin-top: 1rem;
    }
}

@media(max-width: 800px) {
    .pedirOrcamentoOverlay {
        width: 85%;
        height: 85%;
        padding: 2rem;
    }

    .variasEscolhas {
        flex-direction: column;
    }
}


@media(max-width: 470px){
    .nav{
        height: 3.5rem;
        padding: 0 1rem;
    }
    .imageLogo img{
        width: 100px;
    }

    .feature{
        margin-top: 4rem;
    }

    .featureIMG{
        width: 18rem;
    }

    .conteudoFeature {
        margin-top: unset;
    }

    .h3Azul {
        font-size: 0.88rem;
    }

    .botaoOrcamento{
        display: none;
        border-radius: 2px;
        font-size: 1rem;
    }

    .extensaoBotao{
        display: block;
        width: 85%;
    }

    .FAQScontainer {
        width: 95%;
    }

    .accordionItem h4{
        font-size: 1.1rem;
    }

    .accordionContent {
        font-size: 0.9rem;
    }

    .pedirOrcamentoOverlay p {
        font-size: 1.1rem;
    }

    .politicaETermos h2 {
        font-size: 1.5rem;
    }
}

@media(max-width: 350px) {
    .featureIMG{
        width: 17rem;
    }

    .barraAzul h3 {
        padding: 0 0.5rem;
    }
}