.redesSociaisFooter {
    margin-top: 1rem;
}

.redesSociaisFooter p {
    font-size: 1.5rem;
}

.redesSociaisFooter a {
    text-decoration: none;
    transition: 0.2s;
}

.iconsFooter {
    margin-top: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.redesSociaisFooter svg {
    transition: 0.2s;
}

.redesSociaisFooter svg:hover {
    color: #3b7fa1;
}