.accordionItem h4 {
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 100%;
    padding: 1.5rem 3rem 1.5rem 1rem;
    color: #000;
    font-size: 1.5rem;
    border-bottom: 1px solid #bebebe;
}

.accordionItem h4:hover,
.accordionItem h4:hover::after {
    cursor: pointer;
    color: #1d6792;
}

.accordionItem h4:hover::after {
    border: 1px solid #1d6792;
}

.accordionItem h4.active {
    color: #1d6792;
    border-bottom: 1px solid #1d6792;
}

.accordionItem h4::after {
    content: '+';
    position: absolute;
    float: right;
    right: 1rem;
    bottom: 1.1rem;
    font-size: 1.8rem;
    color: #000;
    padding: 5px;
    width: 1.5rem;
    height: 1.5rem;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    border: 1px solid #000;
    display:flex;
    flex-direction:row;
    align-items:center;
    justify-content:center;
}

.accordionItem h4.active::after {
    content: '-';
    color: #1d6792;
    border: 1px solid #1d6792;
}

.accordionContent {
    opacity: 0;
    padding: 0 1rem;
    max-height: 0;
    border-bottom: 1px solid #e5e5e5;
    overflow: hidden;
    clear: both;
    -webkit-transition: all 0.2s ease 0.15s;
    -o-transition: all 0.2s ease 0.15s;
    transition: all 0.2s ease 0.15s;
}

.accordionContent.active {
    opacity: 1;
    padding: 1rem;
    max-height: 100%;
    -webkit-transition: all 0.35s ease 0.15s;
    -o-transition: all 0.35s ease 0.15s;
    transition: all 0.35s ease 0.15s;
}

.accordionContent p {
    line-height: 150%;
    color: #333;
}
