.politicaETermos {
    padding-top: 5rem;
    width: 75%;
    max-width: 71rem;
    margin: 0 auto;
    line-height: 160%;
    color: #333;
    margin-bottom: 7rem;
}

.politicaETermos h2 {
    margin-top: 5rem;
    font-size: 2.25rem;
    font-weight: 500;
    margin-bottom: 1.5rem;
    line-height: 130%;
}

.politicaETermos h3 {
    margin-top: 4rem;
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 1rem;
}

.politicaETermos p {
    margin-bottom: 1rem;
}

.politicaETermos ol {
    margin-top: 2rem;
    list-style-type: decimal;
    list-style-position: inside;
}

.politicaETermos ul {
    margin-top: 2rem;
    list-style-type: square;
    list-style-position: inside;
}

.politicaETermos li {
    margin-bottom: 1rem;
}

.politicaETermos li:last-child {
    margin-bottom: 3rem;
}

.politicaETermos a {
    text-decoration: none;
    color: #59BCEE;
}