.feature {
    margin-top: 7rem;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    justify-content: space-between;
    align-items: flex-start;
    text-align: right;
    width: 85%;
    max-width: 81rem;
}

.featureReversa {
    flex-direction: row-reverse;
    text-align: left;
    align-items: flex-end;
}

.textoFeature {
    width: 50%;
}

.h3Azul {
    margin-top: 1rem;
    text-transform: uppercase;
    font-size: 1.125rem;
    color: #264B81;
    font-weight: 500;
}

.tituloFeature {
    margin-top: 1rem;
    font-size: 2.25rem;
    font-weight: 500;
}

.conteudoFeature {
    margin-top: 3rem;
    color: #313131;
    line-height: 170%;
}