.contentBoxOverlay {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.25);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 12000;
    top: 0;
    left: 0;
    display: none;
}

.pedirOrcamentoOverlay {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    padding: 3rem;
    border-radius: 5px;
    width: 50%;
    height: 60%;
}

.pedirOrcamentoOverlay p {
    font-size: 1.5rem;
    font-weight: 500;
}

.variasEscolhas {
    box-sizing: border-box;
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 95%;
    height: 90%;
}

.pedirOrcamentoOverlay svg {
    fill: #333;
    transition: 0.2s ease;
}

.escolhaDispositivo:hover svg {
    fill: #000;
}